
















































































































































































import Vue from "vue";
import moment from "moment";
import News from "@/models/news";
import DatePicker from "@/components/DatePicker.vue";
import FileManager from "@/components/FileManager.vue";

import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import "@ckeditor/ckeditor5-build-classic/build/translations/de";

export default Vue.extend({
    components: {
        DatePicker,
        FileManager,
    },
    name: "News",
    data() {
        return {
            isLoading: false,
            isCreating: false,
            isSubmitting: false,
            draftNewsEntry: new News(),
            editor: ClassicEditor,
            editorConfig: {
                height: 400,
                language: "de",
                toolbar: [
                    "heading",
                    "bold",
                    "italic",
                    "|",
                    "bulletedList",
                    "numberedList",
                    "|",
                    "link",
                ],
            },
        };
    },
    computed: {
        news() {
            return this.$store.state.news.items;
        },
    },
    filters: {
        moment: function(date) {
            if (!date) {
                return "";
            }
            return moment(date).format("DD. MMMM YYYY");
        },
    },
    created() {
        this.loadNews();
    },
    methods: {
        loadNews() {
            this.isLoading = true;
            this.$store.dispatch("news/load").finally(() => {
                this.isLoading = false;
            });
        },
        createNews() {
            this.draftNewsEntry = new News();
            this.isCreating = true;
        },
        editNews(news) {
            this.draftNewsEntry = news;
            this.isCreating = true;
        },
        submitNews(event) {
            event.preventDefault();
            if (this.isSubmitting) {
                return;
            }

            this.isSubmitting = true;

            const action = this.draftNewsEntry.ID ? "update" : "create";

            this.$store
                .dispatch(`news/${action}`, this.draftNewsEntry)
                .then(news => {
                    this.isCreating = false;
                })
                .finally(() => {
                    this.isSubmitting = false;
                });
        },
        cancelCreateNews() {
            this.isCreating = false;
        },
        deleteNews(news: News) {
            if (window.confirm("Soll der Eintrag wirklich gelscht werden?")) {
                this.$store.dispatch("news/delete", news);
            }
        },
    },
});
